import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Appointment } from './appointment';
import { Product } from './product';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class AppointmentService {

  private readonly baseUrl = 'http://localhost:3000';

  constructor(private http: HttpClient) { }

  getAppointments() {
    return this.http.get(`${this.baseUrl}/api/appointments`)
      .pipe(map((appointments: Appointment[]) => appointments)).toPromise();
  }

  getAppointmentById(appointmentid: string) {
    
    const url = `${this.baseUrl}/api/appointments/${appointmentid}`;

    return this.http.get(url)
      .toPromise()
      .then(response => response as Appointment)
      .catch(this.handleError);
  }

  addAppointment(formData: Appointment) {

    const url = `${this.baseUrl}/api/appointments`;

    return this.http.post(url, formData)
      .toPromise()
      .then(response => response as Appointment)
      .catch(this.handleError);
  }

  updateAppointment(putAppointment: Appointment): Promise<void | Appointment> {
    const url = `${this.baseUrl}/api/appointments/${putAppointment._id}`;
   
    return this.http.put(url, putAppointment)
      .toPromise()
      .then(response => response as Appointment)
      .catch(this.handleError);
  }

  deleteAppointment(appointmentid: string) {
    const url = `${this.baseUrl}/api/appointments/${appointmentid}`;

    return this.http.delete(url)
      .toPromise()
      .catch(this.handleError);
  }



  getProducts() {
    return this.http.get(`${this.baseUrl}/api/products`)
      .pipe(map((products: Product[]) => products)).toPromise();
  }

  getProductById(productid: string) {
    
    const url = `${this.baseUrl}/api/products/${productid}`;

    return this.http.get(url)
      .toPromise()
      .then(response => response as Product)
      .catch(this.handleError);
  }

  private handleError(error) {
    console.log(error);
  }
}

